.app-header {
  background-color: #282c34;
  padding: 10px;
  text-align: center;
  color: white;
}
.app-header h1{
  font-size: 32px;
}
.content-container {
  padding: 20px;
  text-align: center;
}

.search-bar1 {
  margin-bottom: 20px;
}

.search-bar1 input {
  padding: 10px;
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.search-bar1 button {
  padding: 10px 15px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px;
}

.search-bar1 button:hover {
  background-color: #1a1a1a;
}

.video-list {
  display: flex;
  flex-direction: row; /* 가로로 정렬 */
  gap: 20px; /* 카드 간격 */
  overflow-x: auto; /* 가로 스크롤 */
  padding: 10px 0;
}

.video-card {
  min-width: 300px; /* 카드 너비 고정 */
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  color: white;
}

video {
  width: 100%;
  height: auto;
  border-bottom: 1px solid #ccc;
}

.video-card p {
  margin: 10px 0;
  font-size: 16px;
  font-weight: bold;
}

.video-list::-webkit-scrollbar {
  height: 8px; /* 스크롤바 높이 */
}

.video-list::-webkit-scrollbar-thumb {
  background-color: #888; /* 스크롤바 색상 */
  border-radius: 4px;
}

.video-list::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}
/* Footer */
.app-footer1 {


  text-align: center;
  color: white;
  display: column-reverse;
}

.footer-button1 {
  width: 100%;
  bottom: 0%;
  padding: 10px 20px;
  font-size: 16px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  
}

.footer-button1:hover {
  background-color: rgb(36, 36, 36);
}