/* 스플래시 화면 스타일 */
.splash-screen {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #282c34;
    animation: fadeOut 3s forwards; /* 스플래시 화면이 서서히 사라짐 */
  }
  
  /* 스플래시 로고 */
  .splash-logo {
    font-size: 36px;
    color: grey;
    animation: bounce 1.5s infinite; /* 로고가 bounce 효과를 가짐 */
  }
  

  
  /* Bounce 애니메이션 */
  @keyframes bounce {
    0%, 100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-20px);
    }
  }
  
  /* Fade-out 애니메이션 */
  @keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
      display: none;
    }
  }