body {
    margin: 0; /* 기본 여백 제거 */
    padding: 0; /* 기본 패딩 제거 */
    width: 100vw; /* 가로 화면을 뷰포트의 100%로 설정 */
    height: 100vh; /* 세로 화면을 뷰포트의 100%로 설정 */
    overflow-x: hidden; /* 가로 스크롤 방지 */
    font-family: Arial, sans-serif; /* 원하는 폰트 지정 */
    background-color: white; /* 배경 색상 */
    
}

.app-container {
    width: 100%; /* 화면의 가로 전체 */
    max-width: 393px; /* 모바일 최대 너비 */
    margin: 0 auto; /* 화면 가운데 정렬 */
    background-color: #0f1016;
    height: 100vh; /* 세로 화면 전체 */
    
  }