.app-header {
  background-color: #282c34;
  padding: 10px;
  text-align: center;
  color: white;

}
.app-header h1{
  font-size: 32px;
}
.content-container {
  padding: 20px;
  text-align: center;
  height: 100vh; /* 화면 전체 높이 (viewport height) */
  overflow-y: auto; /* 세로 스크롤만 표시 */
  box-sizing: border-box; /* padding 포함 높이 계산 */
}

.search-bar {
  margin-bottom: 20px;

}

.search-bar input {
  padding: 10px;
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.search-bar button {
  padding: 10px 15px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px;
}

.search-bar button:hover {
  background-color: #1a1a1a;
}

.image-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  color: white;

}

.image-card {
  width: 100px;
  height: 100px;
  border: 1px solid #ccc;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  color: white;
}

.image-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Footer */
.app-footer1 {


  text-align: center;
  color: white;
  display: column-reverse;
}

.footer-button1 {
  width: 100%;
  bottom: 0%;
  padding: 10px 20px;
  font-size: 16px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  
}

.footer-button1:hover {
  background-color: rgb(36, 36, 36);
}

/* 모달 스타일 */
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  z-index: 1000;
}

.modal-content {
  position: relative;
  max-width: 80%;
  max-height: 80%;
}

.modal-content img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 15px;
  color: white;
  font-size: 30px;
  font-weight: bold;
  cursor: pointer;
  z-index: 1001;
}

.close-button:hover {
  color: #ccc;
}
