/* 스플래시 화면 스타일 */
.splash-screen {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
  animation: fadeOut 1s forwards;
  /* 스플래시 화면이 서서히 사라짐 */
}

/* 스플래시 로고 */
.splash-logo {
  font-size: 48px;
  color: grey;
  animation: bounce 1s infinite;
  /* 로고가 bounce 효과를 가짐 */
}



/* Bounce 애니메이션 */
@keyframes bounce {

  0%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-20px);
  }
}

/* Fade-out 애니메이션 */
@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    display: none;
  }
}

/* Header */
.app-header {
  display: flex;
  /* Flexbox 사용 */
  justify-content: space-between;
  /* 좌우로 공간 분배 */
  align-items: center;
  /* 수직 방향 가운데 정렬 */
  padding: 10px 20px;
  /* 여백 추가 */
  background-color: #282c34;
  color: white;
}

.header-left h1 {
  font-size: 48px;
  margin: 0;
  white-space: nowrap;
  /* 텍스트가 줄바꿈되지 않도록 설정 */
}

.header-right {
  display: flex;
  /* Flexbox 사용 */
  gap: 10px;
  /* 버튼 사이 간격 */
}

.header-right .header-button {
  text-align: center;
  font-size: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
  background-color: #f9f9f9;
  color: #333;
  transition: all 0.3s ease;
}

.header-right .header-button:hover {
  background-color: #4cafef;
}

/* Tabs */
.tab-menu {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin: 20px 0;
}

.tab-button {
  padding: 10px 20px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
  background-color: #f9f9f9;
  color: #333;
  transition: all 0.3s ease;
}

.tab-button.active {
  background-color: #f9f9f9;
  color: black;
  border-color: #f9f9f9;
}

.tab-button:hover {
  background-color: #0056b3;
  color: white;
}

/* Streaming Screen */
.streaming-screen {
  flex-grow: 1;
  padding: 20px;
  text-align: center;
  background-color: #f0f0f0;
  position: relative;
}

.streaming-content iframe,
.video-content video,
.photo-content img {
  display: block;
  margin: 0 auto 20px auto;
  max-width: 30%;
  border-radius: 5px;
  border: 1px solid #ccc;
}

/* Event List */
.event-list {
  background-color: #222;
  padding: 20px;
  color: black;
}

.event-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #444;
}

.event-item.active {
  background-color: #333;
}

.event-time {
  font-size: 12px;
  color: black;
}

.event-text {
  font-size: 14px;
  font-weight: bold;
  color: #fff;
}

.event-text.active {
  color: #ffc107;
}

.event-status {
  width: 10px;
  height: 10px;
  background-color: #555;
  border-radius: 50%;
}

.event-status.active {
  background-color: #ff5722;
}

.no-events {
  text-align: center;
  color: #888;
  font-size: 14px;
  margin-top: 20px;
}

/* Footer */
.app-footer {
  text-align: center;
  padding: 10px;
  background-color: #282c34;
  color: white;
  bottom: 0;
  display: flex;

}

.footer-button {
  padding: 10px 20px;
  bottom: 0%;
  font-size: 16px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;

}

.footer-button:hover {
  background-color: darkred;
}

/* Contact Popup */
.contact-popup {
  position: absolute;
  bottom: 0%;
  /* Footer 위로 띄움 */
  left: 50%;
  transform: translateX(-50%) translateY(20px);
  width: 200px;
  padding: 10px;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 5px;
  text-align: center;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, transform 0.3s ease, visibility 0.3s ease;
}

.contact-popup.visible {
  opacity: 1;
  visibility: visible;
  transform: translateX(-50%) translateY(0);
}

.contact-link {
  display: block;
  color: #007bff;
  margin: 5px 0;
  text-decoration: none;
}

.contact-link:hover {
  text-decoration: underline;
}

.show-more-button {
  display: block;
  margin: 10px auto;
  padding: 10px 20px;
  font-size: 14px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.show-more-button:hover {
  background-color: #1a1a1a;
}

/* Text Input Container */
.text-input-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  max-width: 500px;
}

.text-input-container input {
  flex-grow: 1;
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
}

.text-input-container button {
  padding: 10px 15px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.text-input-container button:hover {
  background-color: #0056b3;
}

/* 음성 키워드 팝업 */
.voice-popup {
  position: absolute;
  top: 70px;
  right: 20px;
  width: 250px;
  padding: 10px;
  background-color: white;
  color: black;
  border: 1px solid #ddd;
  border-radius: 5px;
  text-align: left;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.voice-popup.visible {
  opacity: 1;
  visibility: visible;
}