/* Global Styles */


.app-container1 {
  display: flex;
  flex-direction: column;
  height: 100vh;
  box-sizing: border-box;
}

/* 스플래시 화면 스타일 */
.splash-screen1 {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
  animation: fadeOut 1s forwards; /* 스플래시 화면이 서서히 사라짐 */
}

/* 스플래시 로고 */
.splash-logo1 {
  font-size: 36px;
  color: grey;
  animation: bounce 1s infinite; /* 로고가 bounce 효과를 가짐 */
}



/* Bounce 애니메이션 */
@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

/* Fade-out 애니메이션 */
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    display: none;
  }
}

/* Header */
.app-header1 {
  display: flex;
  justify-content: center; /* 중앙 정렬 */
  align-items: center; /* 세로 정렬 */
  padding: 10px 20px;
  background-color: #282c34;
  color: white;
  width: 100%;
  box-sizing: border-box;
}

.header1-left h1 {
  font-size: 36px;
  margin: 0;
  white-space: nowrap;
}  

/* Streaming Screen */
.streaming-screen1 {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center; /* 세로 중앙 정렬 */
  align-items: center; /* 가로 중앙 정렬 */
  text-align: center;
  background-color: #444;
  padding: 20px;
  box-sizing: border-box;
}

/* 폼 컨테이너 */
.form-container {
  background-color: #292929;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  color: white;
  width: 100%;
  max-width: 300px; /* 최대 너비 */
  box-sizing: border-box;
  display: flex;
  flex-direction: column; /* 세로로 정렬 */
  justify-content: center;
  align-items: center; /* 가로 중앙 정렬 */
}

/* 제목 */
h1 {
  text-align: center;
  margin-bottom: 15px;
  font-size: 20px; /* 제목 크기 */
  color: white;
}

/* 입력 필드 레이블 */
label {
  display: block;
  margin-bottom: 5px;
  font-size: 14px; /* 텍스트 크기 */
  text-align: left;
  width: 100%;
}

/* 입력 필드 */
input {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #444;
  border-radius: 5px;
  background-color: #121212;
  color: white;
  font-size: 14px; /* 입력 텍스트 크기 */
  box-sizing: border-box;
}

/* 버튼 */
button {
  width: 100%;
  padding: 8px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px; /* 버튼 텍스트 크기 */
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #0056b3;
}
